import _ from "lodash";
import { mapGetters } from "vuex";
export default {
  name: "ScrollListener",
  mounted: function mounted() {
    var throttledScrollHandler = _.throttle(this.scrollhandler, 50);

    window.onscroll = function () {
      // debouncedScrollHandler()
      throttledScrollHandler();
    };

    this.percent = 0;
  },
  data: function data() {
    return {
      offsetTop: 0,
      percent: 0
    };
  },
  methods: {
    scrollhandler: function scrollhandler() {
      this.offsetTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
      this.percent = (this.offsetTop + window.innerHeight) / document.body.offsetHeight * 100;
    }
  },
  activated: function activated() {
    var throttledScrollHandler = _.throttle(this.scrollhandler, 50);

    window.onscroll = function () {
      // debouncedScrollHandler()
      throttledScrollHandler();
    };

    this.percent = 0;
  }
};